import React, { useState } from "react";
import Select from "react-select";
import { useReactFlow } from '@xyflow/react';

const NodeSearch = () => {
  const { getNodes, getNode, setCenter } = useReactFlow();
  const nodes = getNodes(); // Get all nodes in the flow

  const [selectedNode, setSelectedNode] = useState(null);

  const options = nodes.map((node) => ({
    value: node.id,
    label: node.data.label || node.id,
  }));

  const handleSelectChange = (selectedOption) => {
    if(selectedOption===null){return} 
    setSelectedNode(selectedOption);
    moveToNode(selectedOption.value);
  };

  const moveToNode = (nodeId) => {
    const node = getNode(nodeId);
    if (node) {
      const { position, width = 0, height = 0 } = node;
      setCenter(position.x + width / 2, position.y + height / 2, {
        zoom: 1.5,
        duration: 800,
      });
    }
  };

  const customStyles = {
    container: (provided) => ({
      ...provided,
      width: 250,
      position: "fixed",
      top: 10,
      left: "150px",
      transform: "translateX(-50%)",
      zIndex: 1000,
    }),
    control: (provided) => ({
      ...provided,
      minHeight: "30px",
    }),
    valueContainer: (provided) => ({
      ...provided,
      padding: "0 8px",
      textAlign: "center"
    }),
    input: (provided) => ({
      ...provided,
      margin: "0px",
    }),
    indicatorsContainer: (provided) => ({
      ...provided,
      height: "30px",
    }),
  };

  if(nodes.length == 0) {return null}
  return (
    <Select
      value={selectedNode}
      onChange={handleSelectChange}
      options={options}
      placeholder="Search Objects..."
      styles={customStyles}
      isClearable
    />
  );
};

export default NodeSearch;
