import { forceSimulation, forceLink, forceManyBody, forceCenter } from 'd3-force';

export const applyForceDirectedLayout = async (nodes, edges) => {

  var LINK_DISTANCE = 300
  var PULL_STRENGTH_BETWEEN_CONNECTED_NODES = 0.5
  var REPULSION_OR_ATTRACTION_BETWEEN_NODES = -1200 //charge strength

  var CENTER_X = window.innerWidth / 2
  var CENTER_Y = window.innerHeight / 2
  
  var ALPHA_DECAY = 0.02
  var VELOCITY_DECAY = 0.3
  
  var TICKS = 500

  return new Promise((resolve) => {
    // Create a copy of nodes to avoid mutating the original data
    const simulationNodes = nodes.map(node => ({
      ...node,
      x: node.position.x,
      y: node.position.y,
      fx: undefined,
      fy: undefined
    }));

    // Map edges to reference node objects instead of IDs
    const mappedEdges = edges.map(edge => ({
      ...edge,
      source: simulationNodes.find(node => node.id === edge.source),
      target: simulationNodes.find(node => node.id === edge.target)
    }));

    // Create the force simulation
    const simulation = forceSimulation(simulationNodes)
      .force('link', forceLink(mappedEdges)
        .id(d => d.id)
        .distance(LINK_DISTANCE)  // Adjust link distance
        .strength(PULL_STRENGTH_BETWEEN_CONNECTED_NODES)
      )
      .force('charge', forceManyBody().strength(REPULSION_OR_ATTRACTION_BETWEEN_NODES))  // Adjust node repulsion
      .force('center', forceCenter(CENTER_X, CENTER_Y))
      .alphaDecay(ALPHA_DECAY)  // Slow cooling
      .velocityDecay(VELOCITY_DECAY)  // Moderate slowdown
      .stop();

    // Run the simulation
    const numTicks = TICKS;  // Adjust for performance/quality balance
    for (let i = 0; i < numTicks; i++) {
      simulation.tick();
    }

    // Map final positions
    const finalNodes = simulationNodes.map(node => ({
      ...node,
      position: { x: node.x, y: node.y }
    }));

    // Stop the simulation
    simulation.stop();

    // Resolve with the final nodes
    resolve(finalNodes);
  });
};
