import React from 'react';
import { Panel, useReactFlow, getNodesBounds, getViewportForBounds } from '@xyflow/react';
import { toPng } from 'html-to-image';
import { getCustomViewport } from './utils';

function downloadImage(dataUrl) {
  
  const a = document.createElement('a');

  a.setAttribute('download', 'Schematics.png');
  a.setAttribute('href', dataUrl);
  a.click();
}

function saveObjectAsJsonFile(objectData, fileName) {
  const jsonData = JSON.stringify(objectData, null, 2);
  const blob = new Blob([jsonData], { type: 'application/json' });

  // Generate a temporary URL for the Blob
  const url = URL.createObjectURL(blob);

  // Create a temporary link element
  const link = document.createElement('a');
  link.href = url;
  link.download = fileName;

  // Append the link to the DOM and click it programmatically
  document.body.appendChild(link);
  link.click();

  // Remove the link from the DOM after the download starts
  window.requestAnimationFrame(() => {
    URL.revokeObjectURL(url);
    document.body.removeChild(link);
  });
}

function DownloadButton({nodes, edges, setActiveNodeID, legendOn, appVersion}) {
  const { getNodes, getNodesBounds  } = useReactFlow();
  const onClick = () => {
    setActiveNodeID(null)
    console.log(getNodes())

    // we calculate a transform for the nodes so that all nodes are visible
    // we then overwrite the transform of the `.react-flow__viewport` element
    // with the style option of the html-to-image library
    const nodesBounds = getNodesBounds(getNodes());
    console.log(nodesBounds)
    const imageWidth = (nodesBounds.width );
    const imageHeight = (nodesBounds.height );

    const toggleHandles = (hide) => {
      const handles = document.querySelectorAll(".react-flow__handle");
      handles.forEach((handle) => {
        handle.style.display = hide ? "none" : "block";
      });
    };

    toggleHandles(true)

    setTimeout(() => {
      toPng(document.querySelector('.react-flow__viewport'), {
        backgroundColor: 'white',
        width: imageWidth*1.2,  // 1.2 as 0.1 is used as padding in tranfor below
        height: imageHeight*1.2,
        style: {
          alignSelf:'center',
          justifySelf:'center',
          width: imageWidth,
          height: imageHeight,
          transform: `translate(${-(nodesBounds.x)+(imageWidth*0.1)}px, ${-(nodesBounds.y)+(imageHeight*0.1)}px) scale(${1})` 
        },
      })
      .then(downloadImage)
      .finally(() => {toggleHandles(false)})
      ;
    })
  };

  const downloadJSON = () => {
    var object = {nodes: nodes, edges: edges, processed: true, legendOn: legendOn, appVersion: appVersion}
    saveObjectAsJsonFile(object, 'schametics_out')
  }

  if(!nodes[0]){return null}
  return (
    <Panel position="top-right" style={{margin: 0, padding: 9}}>
      <button className="download-btn" onClick={onClick} style={{marginRight: 5}}>
        Download Image
      </button>
      <button className="download-btn" onClick={downloadJSON}>
        Download JSON
      </button>
    </Panel>
  );
}

export default DownloadButton;
